import { Previews } from "../../core/img/Previews"
import {
  isImageMimeType,
  isMimeTypeSupportedByBrowser,
  isVideoMimeType
} from "../../core/tags/MimeTypes"
import { URI } from "../../core/uri/URI"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, toGt0 } from "../../fe/Number"

export interface OpenGraphMetadata {
  property: string
  content: string
}

/**
 * @see https://forum.photostructure.com/t/image-thumbnail-in-link-preview/1950
 * @see https://ogp.me/
 */
export async function getAssetOpenGraph(
  reqUri: URI,
  assetId: Maybe<string | number>
): PromiseMaybe<OpenGraphMetadata[]> {
  assetId = toGt0(assetId)
  if (assetId == null) return

  const ap = Previews.instance()?.ap(assetId)
  if (ap == null) return // no library?

  const info = await ap.readInfo()
  if (info == null) return // missing metadata

  const result: OpenGraphMetadata[] = []

  if (isVideoMimeType(info.mimetype)) {
    result.push({
      property: "og:video",
      content: reqUri.with({ path: "/video/" + assetId }).toString()
    })
    const videoMimetype = (await ap.mp4().exists())
      ? "video/mp4"
      : info.mimetype
    result.push({ property: "og:video:type", content: videoMimetype })
    result.push({ property: "og:video:width", content: info.width.toString() })
  }

  for (const pi of (await ap.previewInfos()).filter(
    ea => ea.reducer === "fit"
  )) {
    result.push({
      property: "og:image",
      content: reqUri
        .with({ path: "/img/" + assetId + "/fit/" + pi.width })
        .toString()
    })
    result.push({ property: "og:image:type", content: "image/jpeg" })
    if (gt0(pi.width)) {
      result.push({ property: "og:image:width", content: pi.width.toString() })
    }
  }

  if (
    isImageMimeType(info.mimetype) &&
    isMimeTypeSupportedByBrowser(info.mimetype)
  ) {
    result.push({
      property: "og:image",
      content: reqUri.with({ path: "/img/" + assetId + "/actual" }).toString()
    })
    result.push({ property: "og:image:type", content: info.mimetype })
    if (gt0(info.width)) {
      result.push({
        property: "og:image:width",
        content: info.width.toString()
      })
    }
  }
  return result
}
