import { uniq } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { Maybe } from "../../fe/MaybeTypes"
import { StrEnumKeys, strEnum } from "../../fe/StrEnum"
import { escapeRegExp } from "../RegExp"

export const ErrorFlags = strEnum(
  "⁰",
  "¹",
  "²",
  "³",
  "⁴",
  "⁵",
  "⁶",
  "⁷",
  "⁸",
  "⁹",
  "₀",
  "₁",
  "₂"
  // "₃",
  // "₄",
  // "₅",
  // "₆",
  // "₇",
  // "₈",
  // "₉"
)
export type ErrorFlag = StrEnumKeys<typeof ErrorFlags>
// I could also go with a ⚠️🛑💥 or "🔁⏹⏏️" ️but these seem less cutesy:

export function sortErrorFlags(arr: Maybe<ErrorFlag>[]): ErrorFlag[] {
  return sortBy(uniq(arr), ea => ErrorFlags.indexOf(ea))
}

export const FatalErrorFlag = ErrorFlags["¹"]
export const NonRetriableErrorFlag = ErrorFlags["²"]
export const IgnorableErrorFlag = ErrorFlags["³"]
export const PleaseSendErrorFlag = ErrorFlags["⁴"]
export const HealthCheckErrorFlag = ErrorFlags["⁵"]
export const DoNotSendErrorFlag = ErrorFlags["⁶"]
export const RetriableErrorFlag = ErrorFlags["⁷"]
export const HealthCheckWarningFlag = ErrorFlags["⁸"]
export const InternalErrorFlag = ErrorFlags["⁹"]
export const NoLibraryErrorFlag = ErrorFlags["₀"]
export const DbSetupErrorFlag = ErrorFlags["₁"]
export const NonFatalErrorFlag = ErrorFlags["₂"]

export const ErrorFlagsRE = new RegExp(
  "(" + ErrorFlags.values.join("|") + ")",
  "g"
)

export const FailStr = JSON.stringify({ fatal: true })

export const FatalErrorPatterns = [
  "SQLITE_FULL",
  "SQLITE_CORRUPT",
  "SQLITE_IOERR", // there are a ton with this prefix, like SQLITE_IOERR_TRUNCATE
  "SQLITE_NOMEM",
  "SQLITE_NOTADB",
  "ON CONFLICT",
  "SQLITE_CONSTRAINT_NOTNULL",
  "Error: Cannot find module",
  FatalErrorFlag,
  FailStr
]

/**
 * Default errors that indicate serious problems
 */
export const FatalErrorRe = new RegExp(
  FatalErrorPatterns.map(escapeRegExp).join("|"),
  "i"
)

export const InternalErrorRe = /⁹|internal ?error/i
